var themeScreenshotIDs = "#default-screenshot, #luna-screenshot, #zond-screenshot";
var themeColorClasses = "default-bg default-accent luna-bg luna-accent zond-bg zond-accent";

$(document).ready(function() {
  // Remove all current theme classes when an example toggle
  $(".theme-wrap").click(function() {
    $("body, .button").removeClass(themeColorClasses);
    $(themeScreenshotIDs).removeClass("current-screenshot");
    $(".theme-wrap").removeClass("current-theme");
    $(this).addClass("current-theme");
  });

  // Adds Default theme classes
  $("#default").click(function() {
    $("body").addClass("default-bg");
    $(".button").addClass("default-accent");
    $("#default-screenshot").addClass("current-screenshot");
  });

  // Adds Luna theme classes
  $("#luna").click(function() {
    $("body").addClass("luna-bg");
    $(".button").addClass("luna-accent");
    $("#luna-screenshot").addClass("current-screenshot");
  });

  // Adds Zond theme classes
  $("#zond").click(function() {
    $("body").addClass("zond-bg");
    $(".button").addClass("zond-accent");
    $("#zond-screenshot").addClass("current-screenshot");
  });
});
